@keyframes flipRight {
  0% {
    transform: rotateY(0);
  }
  100% {
    transform: rotateY(-180deg);
  }
}

@keyframes flipLeft {
  0% {
    transform: rotateY(180deg);
  }
  100% {
    transform: rotateY(0);
  }
}

.page-flip {
  animation: flipRight 1s ease-in-out forwards;
}

.page-flip.prev {
  animation: flipLeft 1s ease-in-out forwards;
}

